.crew_page{
    width: 100vw;
    height: 100vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../assests/crew/background-crew-desktop.jpg');
    display: flex;
    align-items: center;
    justify-content: center;
}

.crew_{
    width: 80%;
    height: 90%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.crew_info{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;/*vertical*/
    align-items: flex-start;/*horizonal*/
    position: relative;
}
.crew_info h3{
    font-family: "Barlow Condensed";
    font-size: 28px;
    color: #fff;
    font-weight: 400;
    letter-spacing: 4px;
}
.crew_info h3 span{
    opacity: 0.5;
    font-weight: bold;
    letter-spacing: 4px;
}
.text h1, .text h2, .text p{
    font-weight: 400;
}
.text h1{
    margin-top: 10px;
    margin-bottom: 20px;
    font-family: Bellefair;
    font-size: 56px;
    color: rgba(255, 255, 255, 1);
}
.text pre{
    font-family: Barlow;
    font-size: 18px;
    color: rgba(208, 214, 249, 1);
    line-height: 180%;
}
.text h2{
     font-family: Bellefair;
     font-size: 32px;
     color: rgba(255, 255, 255, 1);
     opacity: 0.5;
}
.crew_image{
    width: 500px;
    height: 500px;
    object-fit: contain;
}
.navBtn{
    width: 60%;
    height: 40px;
    background-color: #fff;
    position: absolute;
    bottom: 30px;
    left: 0;
}
.navBtn span{
    width: 80px;
    height: 80px;
    background-color: #333;
    border-radius: 50%;
}
.navBtn span {
    width: 15px;
    height: 15px;
    display: inline-block;
    background-color: grey ;
    border-radius: 50%;
    cursor: pointer;
}
.navBtn {
    width: 60%;
    height: 40px;
    display: flex;
    align-items: center;
    gap: 20px;
    background-color: transparent;
    transform: translateY(30px);
}
.crew_photo{
    display: flex;
    justify-content: center;
    align-items: flex-end;
    padding-bottom: 30px;
    position: relative;
}
.crew_photo::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 200px;
    background: linear-gradient(to top, rgba(11, 13, 23, 1) 0%, rgba(11, 13, 23, 0) 100%);
    pointer-events: none;
}
.mobileParagraph{
    padding-left: 20px;
    padding-right: 20px;
    font-family: Barlow;
    font-size: 15px;
    color: rgba(208, 214, 249, 1);
    line-height: 180%;
    text-align: center;

}
.active {
    background-color: #fff !important;
}
.fadeIn{
    animation: fadeIn .5s ease-in-out !important;
}
@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}
@media (max-width: 490px){
    .crew_{
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, 1fr);
    }
    .crew_{
        width: 100vw;
    }
    .crew_info .text{
        transform: translateY(-50px);
    }
    .crew_info h3{
        font-size: 16px;
        width: 100%;
        text-align: center;
    }
    .navBtn{
        left: 50%;
        transform: translateX(-25%);
    }
    .text h2{
        font-size: 18px;
        width: 100%;
        text-align: center;
    }
    .text h1{
        font-size: 24px;
        width: 100%;
        text-align: center;

    }
    .crew_image{
        width: 400px;
        height: 400px;
    }
}
@media (max-width: 400px){
    .crew_info .text{
        transform: translateY(0px);
    }
    .crew_image{
        width: 300px;
        height: 300px;
    }
    .text{
        margin-bottom:30px;
    }
}